import { Controller, Mark } from '@ali/4ever-cangjie';
import { FontsMarkData } from '@ali/4ever-mo';


export const defaultFonts = 'default';
function createEmptyFontsMarkHandler(inheritedFonts?: FontsMarkData) {
  return function emptyFontsMarkHandler() {
    return [Mark.create({
      type: 'fonts',
      data: {
        ascii: defaultFonts,
        hAnsi: defaultFonts,
        cs: defaultFonts,
        eastAsia: defaultFonts,
        ...inheritedFonts,
      },
    })];
  };
}

export default function getActiveFontsMark(controller: Controller, inheritedFonts?: FontsMarkData) {
  return controller.query('getActiveMark', 'fonts', createEmptyFontsMarkHandler(inheritedFonts));
}
